<template>
  <div class="hello">
     <button v-on:click="test">test</button>
  </div>
</template>

<script>

export default {
  name: 'ChatRoom',
  data () {
    return {
     
    }
  },
  sockets: {
      connect: function() {
          console.log('sockets - connect !!!')
      },

      customEmit: function(data) {
          console.log('sockets - customEmit !!! : ' + data)
          console.log('this method was fired by the socket server. eg: io.emit("customEmit", data)')
      }
  },
  methods: {
    test() {
        const data = {
            name: 'aa',
            age: 12
        }
        this.$socket.emit('test', data)
    }

  }
}
</script>



