<template>
  <div id="app">
    <img src="./assets/logo.png">
    <button v-if="ddd.i_user != undefined" v-on:click="logout">로그아웃</button>
    {{this.ddd.i_user}}
    <router-view/>
  </div>
</template>

<script>
import VueCookies from 'vue-cookies'
import final from './final'

export default {
  name: 'App',
  data() {
    return {
      ddd: this.$store.state.user
    }
  },
  beforeCreate() {
    if(this.$store.state.user.i_user == undefined) {
      const token = VueCookies.get(final.cookie.accessToken)

      if(token != undefined) {
        this.$axios.defaults.headers.common['Authorization'] = 'bearer ' + token;
        const user = VueCookies.get(final.cookie.user);
        this.$store.commit('setUser', user)
      }
    }
  },
  methods: {
    logout() {
      const params = {
        i_user: this.$store.state.user.i_user
      }
      console.log('i_user : ' + params.i_user)
      const that = this
      this.$axios.post("/auth/logout", params).then(result => {

        if(result.data == 1) {
          VueCookies.remove(final.cookie.accessToken)
          that.$store.commit('setUser', {})
          that.$router.push('/login')
        }
      }) 
    }
  },
  computed: {
    stateUser: function(){
      return this.$store.state.user
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



